<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>月度报告</span>
      </div>
      {{month}}
<!--      <div id="monthly" style="width: 100%;height: 270px;" />-->
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>年度报告</span>
      </div>
      {{year}}
<!--      <div id="yearly" style="width: 100%;height: 270px;" />-->
    </el-card>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      month:0,
      year:0
    }
  },
  mounted() {
    var that=this;
    axios("api/admin/generateSeasonReport").then(res=>{
      that.month=res.data
      // console.log(res)
    })
    axios("api/admin/generateYearReport").then(res=>{
      that.year=res.data
      // console.log(res)
    })
    var echarts = require('echarts')
    // 初始化echarts实例
    var myChart = echarts.init(document.getElementById('monthly'))
    // 配置参数
    var option = {
      title: {
        text: 'Stacked Area Chart'
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
          label: {
            backgroundColor: '#6a7985'
          }
        }
      },
      legend: {
        data: ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine']
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: [
        {
          name: 'Email',
          type: 'line',
          stack: 'Total',
          areaStyle: {},
          emphasis: {
            focus: 'series'
          },
          data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
          name: 'Union Ads',
          type: 'line',
          stack: 'Total',
          areaStyle: {},
          emphasis: {
            focus: 'series'
          },
          data: [220, 182, 191, 234, 290, 330, 310]
        },
        {
          name: 'Video Ads',
          type: 'line',
          stack: 'Total',
          areaStyle: {},
          emphasis: {
            focus: 'series'
          },
          data: [150, 232, 201, 154, 190, 330, 410]
        },
        {
          name: 'Direct',
          type: 'line',
          stack: 'Total',
          areaStyle: {},
          emphasis: {
            focus: 'series'
          },
          data: [320, 332, 301, 334, 390, 330, 320]
        },
        {
          name: 'Search Engine',
          type: 'line',
          stack: 'Total',
          label: {
            show: true,
            position: 'top'
          },
          areaStyle: {},
          emphasis: {
            focus: 'series'
          },
          data: [820, 932, 901, 934, 1290, 1330, 1320]
        }
      ]
    };
    myChart.setOption(option)
  }
}
</script>



