<template>
  <div>
    <!-- 面包屑导航 -->
    <!-- 搜索筛选 -->
    <el-form ref="form" :model="form" label-width="120px">
      <el-form-item label="活动ID">
        <el-input v-model="form.id" placeholder="请输入活动ID" />
      </el-form-item>
      <el-form-item label="活动名称">
        <el-input v-model="form.name" placeholder="请输入活动名称" />
      </el-form-item>
      <el-form-item label="活动地点">
        <el-input v-model="form.location" placeholder="请输入活动地点" />
      </el-form-item>
      <el-form-item label="报名开始">
        <el-date-picker v-model="form.date1" type="datetime" placeholder="请选择日期" style="width: 100%;" />
      </el-form-item>
      <el-form-item label="活动开始">
        <el-date-picker v-model="form.date2" type="datetime" placeholder="请选择日期" style="width: 100%;" />
      </el-form-item>
      <el-form-item label="活动结束">
        <el-date-picker v-model="form.date3" type="datetime" placeholder="请选择日期" style="width: 100%;" />
      </el-form-item>
      <el-form-item label="活动时长">
        <el-input-number v-model="form.hours" :min="1" :max="24" :precision="1" :step="0.5" @change="handleChange" />
      </el-form-item>
      <el-form-item label="参与人数">
        <el-input-number v-model="form.amount" :min="1" :max="1000" @change="handleChange" />
      </el-form-item>
      <el-form-item label="联系方式">
        <el-input v-model="form.connection" type="textarea" placeholder="请输入联系方式" />
      </el-form-item>
      <el-form-item label="活动描述">
        <el-input v-model="form.desc" type="textarea" placeholder="请输入活动描述" />
      </el-form-item>
<!--      <el-form-item label="活动图片">-->
<!--        <el-upload-->
<!--          class="upload-demo"-->
<!--          action="https://jsonplaceholder.typicode.com/posts/"-->
<!--          :on-preview="handlePreview"-->
<!--          :on-remove="handleRemove"-->
<!--          :file-list="fileList"-->
<!--          list-type="picture"-->
<!--        >-->
<!--          <el-button size="small" type="primary">上传</el-button>-->
<!--          <template #tip>-->
<!--            <div class="el-upload__tip">-->
<!--              这是一个提示-->
<!--            </div>-->
<!--          </template>-->
<!--        </el-upload>-->
<!--      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
  </div>
</template>

<script>
import { deptList, deptSave, deptDelete } from '../../api/userMG'
import Pagination from '../../components/Pagination'
import axios from "axios";
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      form: {
        id:'',
        name: '',
        location: '',
        date1: '',
        date2: '',
        date3: '',
        hours: '',
        amount: '',
        connection: '',
        desc: ''
      },
      // rules表单验证
      rules: {
        deptName: [
          { required: true, message: '请输入部门名称', trigger: 'blur' }
        ],
        deptNo: [{ required: true, message: '请输入部门代码', trigger: 'blur' }]
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 删除部门
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //搜索权限
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    formatDate(date,format){
      let time={
        'M+':date.getMonth()+1,
        'd+':date.getDate(),
        'h+':date.getHours(),
        'm+':date.getMinutes(),
        's+':date.getSeconds(),
        'q+':Math.floor((date.getMonth()+3)/3),
        'S+':date.getMilliseconds()
      };
      if(/(y+)/i.test(format)){
        format=format.replace(RegExp.$1,(date.getFullYear()+'').substr(4-RegExp.$1.length));
      }

      for (let k in time){
        if (new RegExp('('+k+')').test(format)){
          format=format.replace(RegExp.$1,RegExp.$1.length===1?time[k] : ('00'+time[k]).substr((''+time[k]).length));
        }
      }
      return format
    },
    onSubmit(){
      var submit={
        id:parseInt(this.form.id),
        name:this.form.name,
        startTime:this.formatDate(this.form.date2,'yyyy-MM-dd h:m:s'),
        endTime:this.formatDate(this.form.date3,'yyyy-MM-dd h:m:s'),
        location:this.form.location,
        volunteerNum:parseInt(this.form.amount),
        contactWay:this.form.connection,
        hours:parseFloat(this.form.hours),
        restNum:parseInt(this.form.amount)
      }
      axios.post("api/admin/createActivity",submit).then(res=>{
        console.log(res)
      })
    },
    // 获取公司列表
    getdata(parameter) {
      this.loading = true
      // 模拟数据开始
      let res = {
        code: 0,
        msg: null,
        count: 5,
        data: [
          {
            addUser: null,
            editUser: null,
            addTime: 1521062371000,
            editTime: 1526700200000,
            deptId: 2,
            deptName: 'XX分公司',
            deptNo: '1',
            parentId: 1
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1521063247000,
            editTime: 1526652291000,
            deptId: 3,
            deptName: '上海测试',
            deptNo: '02',
            parentId: 1
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1526349555000,
            editTime: 1526349565000,
            deptId: 12,
            deptName: '1',
            deptNo: '11',
            parentId: 1
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1526373178000,
            editTime: 1526373178000,
            deptId: 13,
            deptName: '5',
            deptNo: '5',
            parentId: 1
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1526453107000,
            editTime: 1526453107000,
            deptId: 17,
            deptName: 'v',
            deptNo: 'v',
            parentId: 1
          }
        ]
      }
      this.loading = false
      this.listData = res.data
      this.pageparm.currentPage = this.formInline.page
      this.pageparm.pageSize = this.formInline.limit
      this.pageparm.total = res.count
      // 模拟数据结束

      /***
       * 调用接口，注释上面模拟数据 取消下面注释
       */
      // deptList(parameter)
      //   .then(res => {
      //     this.loading = false
      //     if (res.success == false) {
      //       this.$message({
      //         type: 'info',
      //         message: res.msg
      //       })
      //     } else {
      //       this.listData = res.data
      //       // 分页赋值
      //       this.pageparm.currentPage = this.formInline.page
      //       this.pageparm.pageSize = this.formInline.limit
      //       this.pageparm.total = res.count
      //     }
      //   })
      //   .catch(err => {
      //     this.loading = false
      //     this.$message.error('菜单加载失败，请稍后再试！')
      //   })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = '修改'
        this.editForm.deptId = row.deptId
        this.editForm.deptName = row.deptName
        this.editForm.deptNo = row.deptNo
      } else {
        this.title = '添加'
        this.editForm.deptId = ''
        this.editForm.deptName = ''
        this.editForm.deptNo = ''
      }
    },
    // 编辑、增加页面保存方法
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          deptSave(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.success) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: '公司保存成功！'
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('公司保存失败，请稍后再试！')
            })
        } else {
          return false
        }
      })
    },
    // 删除公司
    deleteUser(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          deptDelete(row.deptId)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: '公司已删除!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('公司删除失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>





