// 导入组件
import Vue from 'vue';
import Router from 'vue-router';
// 登录
import login from '@/views/login';
// 首页
import index from '@/views/index';
/**
 * 基础菜单
 */
// 商品管理
import Goods from '@/views/goods/Goods';
// 货道信息管理
import MachineAisle from '@/views/machine/MachineAisle';
/**
 * 订单管理
 */
// 交易订单
import Order from '@/views/pay/Order';
/**
 * 系统管理
 */
// 用户管理
import user from '@/views/system/user';
// 菜单管理
import Module from '@/views/system/Module';

// 启用路由
Vue.use(Router);

// 导出路由
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
    }, {
        path: '/login',
        name: '登录',
        component: login,
        hidden: true,
    }, {
        path: '/index',
        name: '首页',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/goods/Goods',
            name: '创建活动',
            component: Goods,
        },{
            path: '/pay/Order',
            name: '当前活动',
            component: Order
        }, {
          path: '/pay/Config',
          name: '已结束活动',
          component: Order
        },{
            path: '/system/user',
            name: '活动报告',
            component: user
        }, {
            path: '/system/Module',
            name: '抽签方式',
            component: Module
        }]
    }]
})



